*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
body{
    font-size: 18px;
    font-family: 'Montserrat';
    background: #EEE;
}
.content{
    padding: 2em;
    height: 100vh;
    .login{
        padding: .5em;
        text-align: center;
        width:100%;
        background: url(../img/bg-login-s.svg) no-repeat center;
        background-size: cover;
        height: 90vh;
        color: #EEE;
        border-radius: 20px;
        box-shadow: 3px 3px 10px 3px #BDC3C7;
        color: #2bb6b6ff;
        .content{
            height: 100%;
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            padding: 1em;
            padding-top: 2em;
            form{
                .form-group{
                    margin: 0em 1em;
                    input{
                        width: 100%;
                        height: 100%;
                        margin: 1em 0em;
                        border-radius: 20px;
                        padding: 1em;
                        border: none;
                        box-shadow: 1px 1px 5px 1px #BDC3C7;
                        outline: none;
                    }
                    a{
                        text-decoration: none;
                        color: #2bb6b6ff;
                        margin: 0em .5em;
                        &:hover{
                            color: rgb(29, 119, 119);
                        }
                    }
                }
                .btn{
                    width: 80%;
                    border: none;
                    outline: none;
                    border-radius: 20px;
                    padding: .5em;
                    margin: 1em 0em;
                    color: #EEE;
                    background: -webkit-linear-gradient(160deg, #2bb6b6ff 0%, #65caa8ff 100%);
                    font-size: 20px;
                    transition: transform 1s;
                    &:hover{
                        transform: translateY(-10px);
                    }
                }
            }
            .right{
                display: grid;
                align-items: center;
                .content-right{
                    color: #22313F;
                    h1{
                        margin: .7em 0em;
                    }
                }
            }
        }
    }
}
@media(max-width: 700px){
    .content{
        grid-template-columns: 1fr !important;
    }
}